import React from "react"

import Layout from "../../components/Layout"

import { Link } from "gatsby"

import mea1 from "../../images/monitoraea/monitoraea-content1.png";
import mea2 from "../../images/monitoraea/monitoraea-content2.png";
import mea3 from "../../images/monitoraea/monitoraea-content3.png";

export default function Home() {
  return (
    <Layout breadcrumbs={["MonitoraEA"]}>
      <div id="heading" >
        <h1>MonitoraEA</h1>
      </div>

      {/* Main */}
      <section id="main" className="wrapper">
        <div className="inner">

          <div className="content page">

            {<h5><Link to="/">Inicio</Link> &gt;&gt; Projetos  &gt;&gt; MonitoraEA</h5>}

            <header>
              <h2>Cuidado público com o meio ambiente</h2>
            </header>

            <div style={{ display: 'flex' }}>
              <div>
                <p className="headline">O governo somos todos nós! E políticas públicas de educação ambiental podem ser promovidas por todos que amam o meio-ambiente.</p>
                <p> O MonitoraEA é uma plataforma colaborativa de mapeamento e monitoramento de Políticas Públicas de Educação Ambiental (PPEA) em todo o território brasileiro.</p>
                <p>Partindo do entendimento de que o “público” não pertence ao Estado, e sim à prática da cidadania, o MonitoraEA se coloca como um espaço no qual os diversos atores que se reconhecem como formuladores, proponentes e executores de PPEA no Brasil podem se encontrar, colaborar e dar visibilidade ao tema.</p>
                <p>O MonitoraEA é administrado pela Articulação Nacional de Políticas Públicas de Educação Ambiental (ANPPEA) e desenvolvido pelo Engajados. A plataforma, organizada em torno da colaboração, ainda está em construção, mas já pode ser acessada por quem se interessar.</p>
                <ul>
                  <li>Acesso ao projeto (versão beta) em: <a href="http://monitoraea.org.br/" target="_blank">www.monitoraea.org.br</a></li>
                </ul>
              </div>
              <div>
                <span className="image special"><img src={mea1} alt="" /></span>
                <span className="image special"><img src={mea2} alt="" /></span>
                <span className="image special"><img src={mea3} alt="" /></span>
              </div>
            </div>

          </div>
        </div>
      </section>
    </Layout>
  )
}
